<template>
  <div class="shareholderManagement">
    <v-container
      fluid
      grid-list-xl>

      <v-layout
        row
        wrap>
        <v-flex
          md6
          tag="h3"
          class="mt-0">
          {{ $ln('股東管理') }}
        </v-flex>
        <v-flex
          md6
          class="text-xs-right">
          <v-btn
            small
            color="amber darken-3"
            @click="toUpperLevel()">
            <v-icon class="mr-2">
              mdi-arrow-left
            </v-icon>
            {{ $ln('返回上一層') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <div class="pb-2">
        <!-- 快速查詢帳號 -->
        <div class="d-inline-block mr-3" style="width:280px">
          <v-form v-model="searchAccountItems.valid" @submit="" onSubmit="return false;">
            <v-layout
              wrap
              row>
              <v-flex
                xs8
                class="pr-0">
                <v-text-field
                  v-model="searchAccountItems.account"
                  :rules="searchAccountItems.rules"
                  :label="$ln('快速查詢帳號')"
                  required
                />
              </v-flex>

              <v-flex
                xs4
                class="pt-4 px-0">
                <v-btn
                  small
                  color="deep-orange darken-1"
                  class="mt-2"
                  :disabled="!searchAccountItems.valid || loadFilterMenuStatus"
                  @click="search()">{{ $ln('查找') }}</v-btn>
              </v-flex>
            </v-layout>
          </v-form>
        </div>

        <!-- 每頁顯示 -->
        <div class="d-inline-block mr-3" style="width:150px">
          <v-select
            v-model="selectRowsPerPageItems.selected"
            :items="selectRowsPerPageItems.options"
            :disabled="loadFilterMenuStatus"
            :label="$ln('每頁顯示')"
          />
        </div>

        <!-- 選擇狀況 -->
        <div class="d-inline-block mr-3" style="width:150px">
          <v-select
            v-model="selectStatusItems.selected"
            :items="selectStatusItems.options"
            :disabled="loadFilterMenuStatus"
            :label="$ln('選擇狀態')"
          />
        </div>

        <!-- 新增股東 -->
        <div class="d-inline-block mr-3" v-if="superAccountList.show && showAddAccount">
          <AppendSuperAccount
            :up-account="checkUpAccount()"
            @status="appendStatus"
          />
        </div>
      </div>

      <!-- 層級帳號選擇 -->
      <v-layout
        row
        wrap
        class="levelMenu">
        <v-flex
          md10>
          <v-layout
            row
            wrap
            class="shareholderMenu">
            <v-flex
              v-if="showDirectorSelect()"
              md3
              xs12>
              <v-select
                v-model="selectDirectorItems.selected"
                :items="selectDirectorItems.options"
                :disabled="!selectEnable.director"
                :no-data-text="$ln('無帳號資訊')"
                :label="$ln('選擇總監')"
              />
            </v-flex>

            <div
              v-if="showDirectorSelect()"
              class="maginChevronRight">
              <v-icon>mdi-chevron-right</v-icon>
            </div>

            <v-flex
              md3
              xs12>
              <v-select
                v-model="selectMajorShareholderItems.selected"
                :items="selectMajorShareholderItems.options"
                :disabled="!selectEnable.majorShareholder"
                :no-data-text="$ln('無帳號資訊')"
                :label="$ln('選擇大股東')"
              />
            </v-flex>
          </v-layout>
        </v-flex>

        <v-flex
          md2
          xs12
          class="text-md-right">
          <v-btn
          small
          color="deep-orange darken-1"
          @click="clearSelect()">{{ $ln('清除選擇') }}</v-btn>
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>
        <div
          v-if="loadingTable"
          class="text-xs-center">
          <MaterialLoadingPage/>
        </div>
        <div
          v-else
          v-show="superAccountList.show"
          class="pt-2">
          <div
            v-if="parseInt(pagination.totalItems) > 25 && parseInt(selectRowsPerPageItems.selected) > 10 && pages > 0"
            class="text-xs-center pt-2">
            <v-pagination
              v-model="pagination.currentPage"
              :length="pages"
              :total-visible="pagination.totalVisible"
              color="deep-orange darken-1"
              @input="loadNextPage"/>
          </div>

          <div v-show="pagination.totalItems">
            <p class="category d-inline-flex font-weight-light">{{ $ln(`共 %s 位股東`, pagination.totalItems) }}</p>
          </div>

          <!-- 列表 -->
          <CommonTable
            :Headers="superAccountList.headers"
            :setClass="`no-rwd-table${' ' + rowsTableHeightClass}`">
            <template slot="tbody" v-if="Object.keys(superAccountList.contents).length > 0">
              <tr v-for="(item, i) in superAccountList.contents" :key="i">
                <td class="text-xs-center">{{ item.id }}</td>
                <td class="text-xs-center">
                  <a href="javascript:void(0);" @click="toEdit({ account: item.l_no, upAccount: checkUpAccount(), searchStatus: searchAccountItems.searchStatus, page: pagination.currentPage, rowsPerPage: selectRowsPerPageItems.selected, selectStatus: selectStatusItems.selected } )" >{{ item.l_no }}</a>
                </td>
                <td class="text-xs-center minWidth100">{{ item.l_name }}</td>
                <!-- 體育額度 -->
                <td class="text-xs-center">{{ item.quota.LCtotal | amountFormat }}</td>
                <!-- BingoStar額度 -->
                <td
                  v-show="item.hasOwnProperty('quota') && item.quota.hasOwnProperty('bingo') && superAccountList.bingoStarShow"
                  class="text-xs-center">{{ bingoAmountFormat(item.quota.bingo) }}</td>
                <!-- 皇家額度 -->
                <td
                  v-show="item.hasOwnProperty('quota') && item.quota.hasOwnProperty('royal') && superAccountList.royalShow"
                  class="text-xs-center">{{ royalAmountFormat(item.quota.royal) }}</td>
                <td class="text-xs-center">
                  <AccountManagementSetAllowLoginIndex
                    :login-status="item.l_status"
                    :account="item.l_no"
                    :level="levelInfo.level"
                    @status="setLoginStatus"
                  />
                </td>
                <td class="text-xs-center">
                  <AccountManagementSetAllowBetIndex
                    :bet-status="item.l_close"
                    :account="item.l_no"
                    :level="levelInfo.level"
                    @status="setBetStatus"
                  />
                </td>
                <td class="text-xs-left minWidth630">
                  <span
                    v-for="(value, key, i) in item.occupy"
                    :key="key"
                    class="pr-1">
                    {{ $ln(mapSportNameApi[key]) }}：{{ value }}%{{ i === (Object.keys(item.occupy).length - 1) ? '' : ',' }}
                  </span>
                </td>
                <td class="text-xs-center">
                  <router-link :to="{ path: '/manage/account-management/general-agent-management', query: { upAccount: item.l_no } }">{{ item.down_count }}</router-link>
                </td>
                <td class="text-xs-center minWidth100">{{ item.remark }}</td>
                <td class="text-xs-center">{{ item.err_count }}</td>
                <td class="text-xs-center minWidth50">
                  <router-link :to="{ path: '/manage/account-management/shareholder-management/copy', query: { item: item, upAccount: checkUpAccount(), searchStatus: searchAccountItems.searchStatus } }" class="light-blue--text darken-2--text">
                    {{ $ln('複製') }}
                  </router-link>
                </td>
              </tr>
            </template>
            <template slot="tbody" v-else>
              <tr>
                <td colspan="13" class="text-xs-center">{{ $ln(superAccountList.showNoData) }}</td>
              </tr>
            </template>
          </CommonTable>

          <div v-show="pagination.totalItems">
            <p class="category d-inline-flex font-weight-light">{{ $ln(`共 %s 位股東`, pagination.totalItems) }}</p>
          </div>

          <div
            v-if="pages > 0"
            class="text-xs-center pt-2">
            <v-pagination
              v-model="pagination.currentPage"
              :length="pages"
              :total-visible="pagination.totalVisible"
              color="deep-orange darken-1"
              @input="loadNextPage"/>
          </div>
        </div>

      </div>
    </v-container>
  </div>
</template>

<script>
import { common } from '@/utils'
import AppendSuperAccount from './Append'
import List from '@/mixins/account-management/level-management/List'

export default {
  name: 'ShareholderManagement',
  mixins: [List],
  components: {
    AppendSuperAccount
  },
  data () {
    return {
      // 顯示新增帳號及複製帳號功能
      showAddAccount: true,
      // 選單 enable 狀態
      selectEnable: {
        director: true,
        majorShareholder: false
      },
      // 選擇總監
      selectDirectorItems: {
        selected: '',
        options: []
      },
      // 選擇大股東
      selectMajorShareholderItems: {
        selected: '',
        options: []
      },
      // 層級路線(從上層頁連結至此使用或從下層返回顯示)
      levelRouter: {
        show: false,
        selectDirector: '',
        selectMajorShareholder: ''
      },
      // 載入全部選單狀態
      loadSelectAllStatus: false,
      // 取得資料表參數(大股東)
      levelInfo: {
        // 帳號
        account: '',
        // 層級
        level: 4 // 會員等級: 股東
      },
      // 載入選擇帳號資料列表
      superAccountList: {
        // 依照層級選單顯示或隱藏資料列表
        show: false,
        // 無資料顯示
        showNoData: '目前無資料',
        headers: [
          { text: '#', align: 'center', sortable: false },
          { text: '股東帳號', align: 'center', sortable: false },
          { text: '股東名稱', align: 'center', sortable: false },
          { text: `體育額度${sessionStorage.getItem('super.manage.dashboard.info.unit2')}`, align: 'center', sortable: false },
          { text: `BingoStar額度${sessionStorage.getItem('super.manage.dashboard.info.unit2')}`, align: 'center', sortable: false, type: 's16' },
          { text: `皇家額度${sessionStorage.getItem('super.manage.dashboard.info.unit2')}`, align: 'center', sortable: false, type: 's18' },
          { text: '允許登入', align: 'center', sortable: false },
          { text: '下線允許投注', align: 'center', sortable: false },
          { text: '拆帳百分比', align: 'center', sortable: false },
          { text: '總代理數', align: 'center', sortable: false },
          { text: '備註', align: 'center', sortable: false },
          { text: '登入錯誤次數', align: 'center', sortable: false },
          { text: '功能', align: 'center', sortable: false }
        ],
        contents: [],
        // 顯示BingoStar
        bingoStarShow: true,
        // 顯示皇家
        royalShow: true
      }
    }
  },
  watch: {
    // 選擇總監
    'selectDirectorItems.selected' (value) {
      if (this.loadSelectAllStatus) return
      if (value) {
      // 載入選擇大股東選單
      this.getDownAccount({ account: value })
        .then(res => {
          if (res.status === 200) {
            if (res.data.hasOwnProperty('result')) {
              // 選單變為 enable 狀態
              this.selectEnable.majorShareholder = true
              // 初始下層選單
              this.selectMajorShareholderItems.selected = ''
              this.selectMajorShareholderItems.options = []
              for (let level in res.data.result.accountLevel) {
                if (res.data.result.accountLevel[level].length === 0) return
                for (let account in res.data.result.accountLevel[level]) {
                  this.selectMajorShareholderItems.options.push({
                    text: res.data.result.accountLevel[level][account],
                    value: account
                  })
                }
              }
            }
          }
        })
        .catch(err => {
          console.error(err)
        })

      this.levelRouter.show = false
      this.levelRouter.selectDirector = value
      }
    },
    // 選擇大股東 (載入資料)
    'selectMajorShareholderItems.selected' (value) {
      if (this.loadSelectAllStatus) return

      if (value) {
        // 清除搜尋帳號
        this.searchAccountItems.account = ''
        // 清除搜尋狀態
        this.searchAccountItems.searchStatus = false

        // 欲搜尋股東的大股東帳號
        this.levelInfo.account = value
        // 顯示資料列表
        this.superAccountList.show = true
        // 回第一頁
        this.pagination.currentPage = 1
        // 依照選擇帳號搜尋列表
        this.loadSuperAccount()
        // 載入狀態(開始)
        this.loadingStatus = true
        // 顯示新增帳號及複製帳號功能
        this.showAddAccount = true

        this.levelRouter.show = false
        this.levelRouter.selectMajorShareholder = value
      } else {
        // 清除欲搜尋股東的大股東帳號
        this.levelInfo.account = ''
      }
    }
  },
  mounted () {
    if (parseInt(sessionStorage.getItem('super.manage.level')) === 7) {
      // 載入選擇總監選單
      this.loadDirectorAccount()
      // 初始 disable 狀態
      this.selectEnable = {
        director: true,
        majorShareholder: false
      }
    } else if (parseInt(sessionStorage.getItem('super.manage.level')) === 6) {
      // 載入選擇大股東選單
      this.loadMajorShareholderAccount()
      // 初始 disable 狀態
      this.selectEnable = {
        director: false,
        majorShareholder: true
      }
    }

    // 登入總監以下層級(大股東、股東、總代理、代理)無法連至此頁
    if (parseInt(sessionStorage.getItem('super.manage.level')) < 6) {
      // 轉跳首頁
      this.$router.push({
        path: '/home'
      })
    }

    // 從大股東管理依照帳號連結載入列表，或從編輯帳號返回載入列表
    if (this.$route.query.upAccount || this.$route.query.up5) {
      // 載入全部選單狀態(開始)
      this.loadSelectAllStatus = true
      // 載入篩選選單(開始)
      this.loadFilterMenuStatus = true

      this.searchMember({ account: this.$route.query.upAccount || this.$route.query.up5 })
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
              res.hasOwnProperty('response') &&
              res.response.status !== 200) {
            console.error(res.response)
          } else if (res.status === 200) { // success
            if (res.data.hasOwnProperty('result')) {
              // 選擇總監
              this.levelRouter.selectDirector = res.data.result.list[0].up_no5
              // 選擇大股東
              this.levelRouter.selectMajorShareholder = this.$route.query.upAccount || this.$route.query.up5
              this.levelRouter.show = true

              // 欲搜尋下線列表的大股東帳號
              this.levelInfo.account = this.$route.query.upAccount || this.$route.query.up5

              // 顯示資料列表
              this.superAccountList.show = true
              // 編輯頁跳回列表-載入記憶頁數
              if (sessionStorage.getItem('account.list.page') && this.$route.query.pageType) this.pagination.currentPage = parseInt(sessionStorage.getItem('account.list.page'))
              else this.pagination.currentPage = 1
              // 編輯頁跳回列表-載入記憶每頁筆數
              if (sessionStorage.getItem('account.list.rowsPerPage') && this.$route.query.pageType) {
                this.selectRowsPerPageItems.selected = parseInt(sessionStorage.getItem('account.list.rowsPerPage'))
                this.pagination.rowsPerPage = parseInt(sessionStorage.getItem('account.list.rowsPerPage'))
              }
              // 編輯頁跳回列表-載入記憶每頁筆數
              if (sessionStorage.getItem('account.list.selectStatus') !== null && this.$route.query.pageType) {
                this.selectStatusItems.selected = common.isNumber(sessionStorage.getItem('account.list.selectStatus')) ? parseInt(sessionStorage.getItem('account.list.selectStatus')) : sessionStorage.getItem('account.list.selectStatus')
              }
              // 依照選擇帳號搜尋列表
              this.loadSuperAccount()
              // 載入狀態(開始)
              this.loadingStatus = true

              setTimeout(() => {
                // 清除 pageType
                this.clearQuery('pageType')
              }, 1000)

              // 取得選擇帳號的各上層選單 API
              this.getDownAccount({ accountLine: this.$route.query.upAccount })
                .then(res => {
                  if (res.status === 200) {
                    if (res.data.hasOwnProperty('result')) {
                      if (parseInt(sessionStorage.getItem('super.manage.level')) >= 7) {
                        // 選擇總監
                        this.selectDirectorItems.selected = res.data.result.accountLine['6']

                        // 初始大股東選單
                        this.selectMajorShareholderItems.options = []
                        this.selectMajorShareholderItems.options.push({
                          text: res.data.result.accountLine2['5'],
                          value: res.data.result.accountLine['5']
                        })
                        // 選擇大股東
                        this.selectMajorShareholderItems.selected = res.data.result.accountLine['5']
                      } else if (parseInt(sessionStorage.getItem('super.manage.level')) >= 6) {
                        // 選擇大股東
                        this.selectMajorShareholderItems.selected = res.data.result.accountLine['5']
                      }

                      // 更新選單 enable 狀態
                      this.selectEnable.director = true
                      this.selectEnable.majorShareholder = true

                      let self = this
                      setTimeout(function () {
                        // 載入全部選單狀態(結束)
                        self.loadSelectAllStatus = false
                      }, 1000)
                    }
                  }
                })
                .catch(err => {
                  console.error(err)
                })
            }
          }
          // 載入篩選選單(結束)
          this.loadFilterMenuStatus = false
        })
        .catch(err => {
          console.error(err)
          // 載入篩選選單(結束)
          this.loadFilterMenuStatus = false
        })
    }
  },
  methods: {
    /**
     * 前往編輯頁
     */
    toEdit (query) {
      // add a faker router
      this.$router.push({
        path: `${this.$route.path}`,
        query: {upAccount:query.upAccount}
      })

      this.$router.push({
        path: `${this.$route.path}/edit`,
        query: query
      })
    },
    toUpperLevel () {
      if (parseInt(sessionStorage.getItem('super.manage.level')) === 6) {
        this.$router.push({
          path: '/manage/account-management/major-shareholder-management/directList'
        })
      } else {
        this.$router.push({
          path: '/manage/account-management/major-shareholder-management',
          query: {
            up6: this.levelRouter.selectDirector,
            up5: this.levelRouter.selectMajorShareholder,
            // 上上層(總監)帳號
            upAccount: this.levelRouter.selectDirector
          }
        })
      }
    },
    loadDirectorAccount () {
      // 載入選擇總監選單
      this.getDownAccount({ account: this.checkLoginAccount() })
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
              res.hasOwnProperty('response') &&
              res.response.status !== 200) {
            if (res.response.status === 401) return
            // 錯誤訊息
            if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
            else this.$toastr.error(this.$ln(`載入總監選單失敗`))
            console.error(res.response)
            // disable 選單
            this.selectEnable.director = false
          } else if (res.status === 200) { // success
            // http 200 回應錯誤訊息
            if (res.data.hasOwnProperty('errors')) {
              console.error('response 200 error: ', res.data)
              this.$toastr.error(this.$ln(`載入總監選單失敗`))
              // disable 選單
              this.selectEnable.director = false
            } else if (res.data.hasOwnProperty('result')) {
              // 初始選單
              this.selectDirectorItems.options = []
              for (let level in res.data.result.accountLevel) {
                for (let account in res.data.result.accountLevel[level]) {
                  this.selectDirectorItems.options.push({
                    text: res.data.result.accountLevel[level][account],
                    value: account
                  })
                }
              }
            }
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    loadMajorShareholderAccount () {
      // 載入選擇大股東選單
      this.getDownAccount({ account: this.checkLoginAccount() })
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
              res.hasOwnProperty('response') &&
              res.response.status !== 200) {
            if (res.response.status === 401) return
            // 錯誤訊息
            if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
            else this.$toastr.error(this.$ln(`載入大股東選單失敗`))
            console.error(res.response)
            // disable 選單
            this.selectEnable.majorShareholder = false
          } else if (res.status === 200) { // success
            // http 200 回應錯誤訊息
            if (res.data.hasOwnProperty('errors')) {
              console.error('response 200 error: ', res.data)
              this.$toastr.error(this.$ln(`載入大股東選單失敗`))
              // disable 選單
              this.selectEnable.majorShareholder = false
            } else if (res.data.hasOwnProperty('result')) {
              // 初始選單
              this.selectMajorShareholderItems.options = []
              for (let level in res.data.result.accountLevel) {
                for (let account in res.data.result.accountLevel[level]) {
                  this.selectMajorShareholderItems.options.push({
                    text: res.data.result.accountLevel[level][account],
                    value: account
                  })
                }
              }
            }
          }
        })
        .catch(err => {
          console.error(err)
        })
    },
    clearSelect () {
      this.selectInit()
      // 隱藏資料列表
      this.superAccountList.show = false
    },
    selectInit () {
      if (parseInt(sessionStorage.getItem('super.manage.level')) === 7) {
        // 初始選擇總監選單
        this.selectDirectorItems.selected = ''
        this.loadDirectorAccount()
        // 初始下層選單
        this.selectMajorShareholderItems.selected = ''
        this.selectMajorShareholderItems.options = []
        // 初始 disable 狀態
        this.selectEnable = {
          director: true,
          majorShareholder: false
        }
      } else if (parseInt(sessionStorage.getItem('super.manage.level')) === 6) {
        // 初始選擇大股東選單
        this.selectMajorShareholderItems.selected = ''
        this.loadMajorShareholderAccount()
        // 初始 disable 狀態
        this.selectEnable = {
          director: false,
          majorShareholder: true
        }
      }
    },
    /** 載入管端帳號列表
     * @param searchAccount
     **/
    loadSuperAccount (searchAccount) {
      let params = {
        l_lv: this.levelInfo.level, // 會員等級: 股東
        l_no: !searchAccount ? this.levelInfo.account : '', // 篩選帳號
        l_status: this.selectStatusItems.selected,
        page: this.pagination.currentPage,
        perPage: this.pagination.rowsPerPage
      }
      // 搜尋帳號
      if (searchAccount) params.searchAccount = searchAccount
      try {
        this.getSuperAccount(params)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(this.$ln(`取得管端帳號列表失敗`))
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得管端帳號列表成功`)
              }
            }
            // 載入狀態(結束)
            this.loadingStatus = false
            // 表格載入狀態(結束)
            this.loadingTable = false
          })
          .catch(err => {
            console.error(err)
            // 載入狀態(結束)
            this.loadingStatus = false
            // 表格載入狀態(結束)
            this.loadingTable = false
          })
      } catch (error) {
        this.$toastr.error(this.$ln(`站台資料錯誤，請聯絡客服`))
        console.error(error)
      }
    },
    search () {
      // 初始選單
      this.selectInit()
      // 顯示資料列表
      this.superAccountList.show = true
      // 回第一頁
      this.pagination.currentPage = 1

      // 搜尋列表
      if (this.searchAccountItems.account) {
        this.loadSuperAccount(this.searchAccountItems.account)
        // 表格載入狀態(開始)
        this.loadingTable = true
        // 設置搜尋狀態
        this.searchAccountItems.searchStatus = true

        // 若是搜尋帳號，取得搜尋帳號的上層帳號
        this.getDownAccount({ accountLine: this.searchAccountItems.account })
          .then(res => {
            if (res.status === 200) {
              if (res.data.hasOwnProperty('result') && res.data.result.hasOwnProperty('accountLine')) {
                // 股東上上層(總監)層級6
                this.levelRouter.selectDirector = res.data.result.accountLine['6']
              }
            }
          })
      } else {
        this.$toastr.error(this.$ln(`您查詢的帳號為空值`))
        this.pagination.currentPage = 1
        this.pagination.totalItems = 0
        this.superAccountList.contents = []
      }

      // 隱藏新增帳號及複製帳號功能
      this.showAddAccount = false

      this.levelRouter.show = false
    },
    // 是否顯示總監選單
    showDirectorSelect () {
      if (parseInt(sessionStorage.getItem('super.manage.level')) >= 7) return true
      return false
    },
    // 是否顯示大股東選單
    showMajorShareholderSelect () {
      if (parseInt(sessionStorage.getItem('super.manage.level')) >= 6) return true
      return false
    },
    // 檢視登入等級
    checkLoginLevel () {
      return parseInt(sessionStorage.getItem('super.manage.level'))
    },
    // 檢視登入帳號
    checkLoginAccount () {
      return sessionStorage.getItem('super.manage.account')
    },
    // 檢視預載入列表的上層帳號
    checkUpAccount () {
      if (this.selectMajorShareholderItems.selected) {
        return this.selectMajorShareholderItems.selected
      } else if (this.$route.query.upAccount) {
        return this.$route.query.upAccount
      } else if (this.$route.query.up5) {
        return this.$route.query.up5
      }
    }
  }
}
</script>

