<template></template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'
import numeral from 'numeral'

export default {
  name: "LevelManagementList",
  props: {},
  data: function() {
    // 資料
    return {
      // 站台總類
      wsType: '',
      // 頁面載入狀態
      loadingStatus: false,
      // 表格載入狀態
      loadingTable: false,
      // 快速查詢帳號
      searchAccountItems: {
        valid: false,
        account: '',
        // 搜尋狀態(搜尋出的帳號為跳轉編輯或複製頁面使用)
        searchStatus: false,
        rules: [] // *initLanguage*
        // rules: [
        //   v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || '需為英文或數字，不能為空格'
        // ]
      },
      // 載入篩選選單(每頁顯示、選擇狀態)
      loadFilterMenuStatus: false,
      // 選擇狀態
      selectStatusItems: {
        selected: 'all',
        options: [] // *initLanguage*
        // options: [
        //   { text: '全部', value: 'all' },
        //   { text: '使用中', value: 0 },
        //   { text: '已鎖定', value: 1 }
        // ]
      },
      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 25, // (若調整該值，需更新 rowsTableHeightClass 預設值)
        options: [] // *initLanguage*
        // options: [
        //   { text: '5筆', value: 5 },
        //   { text: '10筆', value: 10 },
        //   { text: '25筆', value: 25 },
        //   { text: '50筆', value: 50 }
        // ]
      },
      // 對應 API 體育代號名稱
      mapSportNameApi: {
        1: '美棒',
        2: '日棒',
        3: '台棒',
        4: '韓棒',
        5: '冰球',
        6: '籃球',
        7: '彩球',
        8: '美足',
        9: '網球',
        10: '足球',
        11: '指數',
        12: '賽馬＆賽狗',
        13: '電競',
        14: '其他',
        15: '世足',
        bingo: 'BingoStar',
        royal: '皇家'
      },
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 25, // 每頁筆數(若調整該值，需更新 rowsTableHeightClass 預設值)
        totalItems: 0, // 總筆數
        totalVisible: 10 // 顯示最大可見頁數按鈕，其他會用 ... 顯示
      },
      // 依照每頁筆數設定高度Class(rows5-table-h, rows10-table-h, rows25-table-h, rows50-table-h)
      rowsTableHeightClass: 'rows5-table-h',
      // 當頁筆數
      currentPageCount: 0
    };
  },
  watch: {
    // 切換語系
    '$store.state.lang' (value) {
      this.initLanguage()
    },
    getSuperAccountState (payload) {
      this.pagination.totalItems = payload.result.totalCount
      // 設置編號(#)
      payload.result.list.forEach((item, i) => {
        item.id = parseInt(this.pagination.rowsPerPage * (this.pagination.currentPage - 1) + (i + 1))
      })
      // 設置列表
      this.superAccountList.contents = payload.result.list
      this.currentPageCount = Object.keys(payload.result.list).length
    },
    // 選擇狀態
    'selectStatusItems.selected' (value) {
      // 非編輯頁 watch 狀態
      if (!this.$route.query.pageType) {
        // 表格載入狀態(開始)
        this.loadingTable = true
        // 回第一頁
        this.pagination.currentPage = 1
        this.loadSuperAccount()
      }
    },
    // 選擇顯示筆數
    'selectRowsPerPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 非編輯頁 watch 狀態
      if (!this.$route.query.pageType) {
        // 表格載入狀態(開始)
        this.loadingTable = true
        // 回第一頁
        this.pagination.currentPage = 1
        this.loadSuperAccount()
      }
    },
    'currentPageCount' (value) {
      this.setTableHeight(this.selectRowsPerPageItems.selected)
    }
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  computed: {
    ...mapGetters({
      getSuperAccountState: 'account-management/getSuperAccountState',
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  methods: {
    ...mapActions({
      getDownAccount: 'api/getDownAccount',
      getSuperAccount: 'api/getSuperAccount',
      searchMember: 'api/searchMember'
    }),
    /**
     * 設定表格高度
     * @param rows 行數
     **/
    setTableHeight (rows) {
      // 5筆：174px
      // 10筆：260px
      // 25筆：512px
      // 50筆：876px
      switch (rows) {
        case 5: // 每頁顯示5筆
          // 最後一頁筆數小於5
          if (this.currentPageCount < 5) this.rowsTableHeightClass = this.lessRowsPerPageSelected(this.currentPageCount)
          else this.rowsTableHeightClass = 'rows5-table-h'

          // 無論最後一頁筆數多少Class設定一樣
          // this.rowsTableHeightClass = 'rows5-table-h'
          break
        case 10: // 每頁顯示10筆
          // 最後一頁筆數小於10
          if (this.currentPageCount < 10) this.rowsTableHeightClass = this.lessRowsPerPageSelected(this.currentPageCount)
          else this.rowsTableHeightClass = 'rows10-table-h'

          // 無論最後一頁筆數多少Class設定一樣
          // this.rowsTableHeightClass = 'rows10-table-h'
          break
        case 25: // 每頁顯示25筆
          // 最後一頁筆數小於25
          if (this.currentPageCount < 25) this.rowsTableHeightClass = this.lessRowsPerPageSelected(this.currentPageCount)
          else this.rowsTableHeightClass = 'rows25-table-h'

          // 無論最後一頁筆數多少Class設定一樣
          // this.rowsTableHeightClass = 'rows25-table-h'
          break
        case 50: // 每頁顯示50筆
          // 最後一頁筆數小於50
          if (this.currentPageCount < 50) this.rowsTableHeightClass = this.lessRowsPerPageSelected(this.currentPageCount)
          else this.rowsTableHeightClass = 'rows50-table-h'

          // 無論最後一頁筆數多少Class設定一樣
          // this.rowsTableHeightClass = 'rows50-table-h'
          break
      }
    },
    /**
     * 當頁少於需顯示的筆數
     * @param currentPageCount 當前筆數
     **/
    lessRowsPerPageSelected (currentPageCount) {
      // @最後一頁若筆數少於每頁顯示筆數要如何顯示?!@
      if (currentPageCount <= 4) { // 0 ~ 4筆
        return 'table-h'
      } else if (currentPageCount <= 9 && currentPageCount >= 5) { // 5 ~ 9筆
        return 'rows5-table-h'
      } else if (currentPageCount <= 15 && currentPageCount >= 10) { // 10 ~ 15筆
        return 'rows10-table-h'
      } else if (currentPageCount <= 24 && currentPageCount >= 16) { // 16 ~ 24筆
        return 'rows25-table-h'
      } else if (currentPageCount <= 37 && currentPageCount >= 25) { // 25 ~ 37筆
        return 'rows25-to-rows37-table-h'
      } else if (currentPageCount <= 49 && currentPageCount >= 38) { // 38 ~ 49筆
        return 'rows50-table-h'
      }
    },
    /**
     * 清除 query 值
     * @param key
     **/
    clearQuery (key) {
      let query = Object.assign({}, this.$route.query)
      delete query[key]
      this.$router.replace({ query })
    },
    /**
     * (BingoStar)額度格式
     * 說明: 賓果額度 > 無條件捨去至小數第二位
     **/
    bingoAmountFormat (value) {
      return numeral(this.toChopOff(value, 2)).format('0,0.00')
    },
    /**
     * (皇家)額度格式(1:現金、2:信用)
     * 說明: 皇家額度 > 四捨五入至小數第四位
     * ## 信用版：四捨五入至第四位
     * ## 現金版：四捨五入至第二位
     **/
    royalAmountFormat (value) {
      if (this.wsType === 1) {
        return numeral(this.toRoundOff(value, 2)).format('0,0.00')
      } else if (this.wsType === 2) {
        return numeral(this.toRoundOff(value, 4)).format('0,0.0000')
      } else {
        return numeral(value).format('0,0.00')
      }
    },
    /**
     * 數值四捨五入
     * @params value      數值
     * @params precision  小數位
     **/
    toRoundOff (value, precision) {
      return common.toRoundOff(value, precision)
    },
    /**
     * 數值無條件捨去
     * @params value      數值
     * @params precision  小數位
     **/
    toChopOff (value, precision) {
      return common.toChopOff(value, precision)
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadSuperAccount()
    },
    // 快速查詢帳號 @每個層級頁處理的不太一樣，不好寫繼承@
    // search () {
    //   // 回第一頁
    //   this.pagination.currentPage = 1
    //
    //   // 搜尋列表
    //   if (this.searchAccountItems.account) {
    //     this.loadSuperAccount(this.searchAccountItems.account)
    //     // 表格載入狀態(開始)
    //     this.loadingTable = true
    //   } else {
    //     this.$toastr.error(this.$ln(`您查詢的帳號為空值`))
    //     this.pagination.currentPage = 1
    //     this.pagination.totalItems = 0
    //     this.superAccountList.contents = []
    //   }
    // },
    // 新增帳號後刷新列表
    appendStatus (status) {
      if (status) this.loadSuperAccount()
    },
    // 設定允許登入刷新列表
    setLoginStatus (status) {
      if (status) {
        if (this.searchAccountItems.account !== '') {
          this.loadSuperAccount(this.searchAccountItems.account)
        } else {
          this.loadSuperAccount()
        }
      }
    },
    // 設定允許投注刷新列表
    setBetStatus (status) {
      if (status) {
        if (this.searchAccountItems.account !== '') {
          this.loadSuperAccount(this.searchAccountItems.account)
        } else {
          this.loadSuperAccount()
        }
      }
    },
    /**
     * 小數值轉換百分比
     * @params point   小數值
     * @params number  轉換後小數位數
     **/
    toPercent (point, number) {
      return common.toPercent(point, number)
    },
    // 更新大後台設置資訊
    updateDashboardInfo () {
      let dashboardInfo = JSON.parse(sessionStorage.getItem('super.manage.dashboard.info'))
      if (dashboardInfo && dashboardInfo.hasOwnProperty('noOpenGame')) {
        // 取得站台種類(1:現金、2:信用)
        this.wsType = dashboardInfo.wsType

        // 不開放遊戲設定(管端列表內容，前端是使用API回傳值綁定表格值，所以表格內容遊戲開關應由後端判斷)
        // 註明：「BingoStar」和「皇家」若不開放，先註解 list 額度欄位(假資料關係)
        for (let i in dashboardInfo.noOpenGame) {
          this.closeGameType(dashboardInfo.noOpenGame[i])
        }
      }
    },
    // 不開放遊戲
    closeGameType (type) {
      // 資料列表表格 header
      let listItems = []
      for (let i in this.superAccountList.headers) {
        if (this.superAccountList.headers[i].type !== type) listItems.push(this.superAccountList.headers[i])
      }
      this.superAccountList.headers = listItems

      // 顯示BingoStar
      if (type === 's16') this.superAccountList.bingoStarShow = false
      // 顯示皇家
      if (type === 's18') this.superAccountList.royalShow = false
    },
    // 初始語系
    initLanguage () {
      // 快速查詢帳號
      this.searchAccountItems.rules = [
        v => ((/^[A-Za-z0-9]+$/.test(v)) || v === '') || this.$ln('需為英文或數字，不能為空格')
      ]

      // 選擇狀態
      this.selectStatusItems.options = [
        { text: this.$ln('全部'), value: 'all' },
        { text: this.$ln('使用中'), value: 0 },
        { text: this.$ln('已鎖定'), value: 1 }
      ]

      // 每頁顯示筆數
      this.selectRowsPerPageItems.options = [
        { text: this.$ln('5筆'), value: 5 },
        { text: this.$ln('10筆'), value: 10 },
        { text: this.$ln('25筆'), value: 25 },
        { text: this.$ln('50筆'), value: 50 }
      ]
    }
  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    this.initLanguage()

    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。
    this.src = this.$options.__file ;
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    //元素已掛載， $el 被建立。
    this.updateDashboardInfo()
  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  beforeDestroy: function() {
    //實體還可使用。
  },
  destroyed: function() {
    //實體銷毀。
  }
  //END--生命週期
};
</script>

<style lang="scss" scoped>
</style>
